"use client";

import React, { useEffect, useState } from "react";
import { Card } from "@/components/ui/card";
import { Tabs, TabsList, TabsTrigger, TabsContent } from "@/components/ui/tabs";
import SalesDashboard from "@/components/dashboard/SalesDashboard";
import TrafficDashboard from "@/components/dashboard/TrafficDashboard";
import AdsDashboard from "@/components/dashboard/AdsDashboard";

export default function DashboardPage() {
  const [salesData, setSalesData] = React.useState<any[]>([]);
  const [trafficData, setTrafficData] = React.useState<any[]>([]);
  const [adsData, setAdsData] = React.useState<any[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    async function loadData() {
      setLoading(true);
      const salesRes = await fetch('/api/sales');
      const sales = await salesRes.json();
      const trafficRes = await fetch('/api/traffic');
      const traffic = await trafficRes.json();
      const adsRes = await fetch('/api/ads');
      const ads = await adsRes.json();
      setSalesData(sales);
      setTrafficData(traffic);
      setAdsData(ads);
      setLoading(false);
    }
    loadData();
  }, []);

  if (loading) {
    return <div className="p-8 text-center text-lg font-semibold">Loading dashboard...</div>;
  }

  return (
    <div className="p-8 max-w-7xl mx-auto">
      <h1 className="text-3xl font-bold mb-6">Dashboard</h1>
      <Card className="p-4">
        <Tabs defaultValue="sales" className="w-full">
          <TabsList>
            <TabsTrigger value="sales">Sales</TabsTrigger>
            <TabsTrigger value="traffic">Traffic</TabsTrigger>
            <TabsTrigger value="ads">Ads</TabsTrigger>
          </TabsList>
          <TabsContent value="sales">
            <SalesDashboard data={salesData} />
          </TabsContent>
          <TabsContent value="traffic">
            <TrafficDashboard data={trafficData} />
          </TabsContent>
          <TabsContent value="ads">
            <AdsDashboard data={adsData} />
          </TabsContent>
        </Tabs>
      </Card>
    </div>
  );
}
